.price-backdrop {
  width: 100%;
  height: 100vh;
  opacity: 1;
  transition: var(--animation);
  z-index: 10000;
  background-color: #2e2f4266;
  position: fixed;
  top: 0;
  left: 0;
}

.price-backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.price-modal {
  max-width: 414px;
  background-color: var(--primary-bg-color-light);
  border: 1px solid var(--accent-color-theme-dark);
  border-radius: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 1024px) {
  .price-modal {
    max-width: 712px;
  }
}

.price-modal-btn-close {
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.price-modal-icon-close {
  width: 24px;
  height: 24px;
  stroke: #000;
  transition: stroke var(--animation);
}

.price-modal-icon-close:hover, .price-modal-icon-close:active {
  stroke: var(--accent-color-theme-dark);
}

.price-form {
  padding: 60px 15px;
}

@media screen and (min-width: 1024px) {
  .price-form {
    padding: 60px;
  }
}

.price-form-title {
  font-family: var(--secondary-font);
  letter-spacing: .05em;
  color: #000;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 40px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.17;
}

@media screen and (min-width: 1024px) {
  .price-form-title {
    font-size: 40px;
    line-height: 1.15;
  }
}

.price-form-room-type {
  font-family: var(--secondary-font);
  letter-spacing: .05em;
  color: gray;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.17;
}

.price-form-box-wrap {
  flex-direction: column;
  row-gap: 20px;
  margin-bottom: 40px;
  display: flex;
}

@media screen and (min-width: 1024px) {
  .price-form-box-wrap {
    flex-direction: row;
    column-gap: 16px;
  }
}

.price-form-box-input {
  width: 100%;
  height: 54px;
  border: 1px solid var(--accent-color-theme-light);
  font-family: var(--secondary-font);
  letter-spacing: .05em;
  color: gray;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  filter: drop-shadow(0 4px 4px #00000040);
  transition: border var(--animation);
  background-color: #0000;
  margin-bottom: 8px;
  padding-left: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
}

@media screen and (min-width: 1024px) {
  .price-form-box-input {
    width: 288px;
    height: 64px;
    font-size: 24px;
    line-height: 28px;
  }
}

.price-form-box-input:hover, .price-form-box-input:focus {
  border: 2px solid var(--accent-color-theme-dark);
}

.price-form-label, .price-form-type-label {
  letter-spacing: .02em;
  color: var(--accent-color-theme-light);
  margin-left: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  display: block;
}

@media screen and (min-width: 1024px) {
  .price-form-label, .price-form-type-label {
    font-size: 16px;
    line-height: 20px;
  }
}

.price-form-type-label {
  margin-bottom: 20px;
}

.price-form-button {
  color: var(--primary-text-color-theme-dark);
  width: 384px;
  height: 64px;
  border: 1px solid var(--accent-color-theme-dark);
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  transition: background-color var(--animation), border-color var(--animation);
  background-color: #000;
  border-radius: 2px;
  padding: 20px 42px;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
}

@media screen and (min-width: 1024px) {
  .price-form-button {
    width: 592px;
  }
}

.price-form-button:hover, .price-form-button:active {
  background-color: var(--accent-color-theme-dark);
}

.price-icon-option {
  width: 24px;
  height: 24px;
  pointer-events: none;
  fill: var(--accent-color-theme-light);
  position: absolute;
  top: 20px;
  right: 20px;
}

